$brk-base-colors: ();
$brk-base-colors: map-merge((
				"brand-primary": 			#0c48ad,
				"secondary": 					#2067dc,
				"white":              #fff,
				"black":              #000,
				"brk-dark-base":      #272727,
				"brk-base-1":         #185bc9,
				"brk-base-2":         #2668d0, //h1
				"brk-base-3":         #0F5AE0,
				"brk-base-4":         #d1ddf2,
				"brk-base-5":         #00f6ff,
				"brk-base-6":         #0B98F6,
				"brk-base-7":         #a7ebff,
				"brk-base-8":         #1600ba,// TODO: !!!
				"brk-secondary-1": 		#000a1a,
				"brk-secondary-2": 		#45474c,
				"brk-secondary-3": 		#0033e0,
				"brk-secondary-4": 		#00a8e0,
				"brk-secondary-5":    #6377DF,
				"brk-secondary-6": 		#0983e0,
				"text-base-color-1":  #546887, //p
				"text-base-color-2":  #0F5AE0,
				"text-base-color-3":  #f3f3f3,
      "form-color-1":         rgba(38, 104, 208, .2)
), $brk-base-colors);

@function safe-rgb($color) {
	@return unquote("#{red($color)}, #{green($color)}, #{blue($color)}");
}

/* ----------- Palette variable ----------- */
:root {
	@each $color, $value in $brk-base-colors {
		--#{$color}: #{$value};
	}

	// RGB
	@each $color, $value in $brk-base-colors {
		--#{$color}-rgb: #{safe-rgb($value)};
	}

	--b-radius: inherit;
}
/* ----------- Palette variable ----------- */
@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.overlay__delimiter {
	opacity: 0.9
}

[class*="delimiter"]{
	figure{
		box-shadow: none;
	}
}

.delimiter {
	&:before,
	&:after {
		content: '';
		width: 100%;
		height: 100%;
	}

	&__circle {
		.services__wrapper-honeycomb .services__wrapper .icon__cover {
			margin-bottom: 30px;
		}

		.services__wrapper-main {
			z-index: 6;
			position: relative;
		}

		.overlay__delimiter {
			&-before {
				left: 0;
				top: -1px;

				svg {
					bottom: auto;
				}
			}
		}
	}

	&__angle {
		padding-bottom: 13.5vw;

		@media #{$xs-max} {
			padding-bottom: 60px;
		}

		.overlay__delimiter {
			&-before {
				left: 0;
				bottom: -2px;
			}
		}

		.icon__btn{
			.icon {
				width: 50%;
			}

			.livicon-evo-holder{ 
				bottom: auto;
				right: auto;
				width: 70% !important;
				top: 50%;
				left: 50%;
        transform: translate(-50%, -50%);
			}
		}
	}

	&__curve {
		padding-bottom: 5vw;

		@media (max-width: 992px) {
			padding-bottom: 120px !important;
		}

		.highlight-underline {
			margin-top: 20px;
		}

		.overlay__delimiter-before {
			left: 0;
			bottom: -2px;
		}
	}

	&__trapezoid {
		.overlay__delimiter {
			&-before {
				left: 0;
				bottom: 0;
				top: -1px;
				//height: 100%;
				width: auto;

				svg {
					bottom: auto;
					height: 100%;
					width: 100%;
				}
			}
		}
	}

	&__corner {
		h1,h2 {
			opacity: .1;
			color: $white;

			@media #{$xs-max} {
				line-height: 25px;
				font-size: 3.4rem;
			}
		}

		.overlay__delimiter {
			&-before {
				left: 0;
				bottom: -2px;
			}
		}

		& * {
			z-index: 5;
			position: relative;
		}
	}

	&__rounded {
		.overlay__delimiter {
			&-after {
				z-index: -1;
			}
			&-before {
				left: 0;
				bottom: -1px;
				z-index: -1;
			}
		}

		.all-light {
			@media #{$xs-max} {
				margin-bottom: 30px;
			}
		}

		p {
			color: #bce1ff;
		}
		.border-btn {
			border-color: var(--brk-base-2) !important;
		}
		.highlight-underline > .before {
			background: var(--brk-base-2) !important;
		}
	}
}

.overlay__delimiter {
	&-before {
		display: block;
		position: absolute;
		left: 0;
		right: 0;
		background-repeat: no-repeat;
		z-index: 5;

		@media #{$xs-max} {
			display: none;
		}

		svg {
			position: relative;
			bottom: -6px;
		}
	}

	&-after {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 3;
	}
}

.container-xl {
	@media (min-width: 1700px) {
		width: 1700px;
		max-width: 1700px;
	}
}